import React, {Component} from 'react';
import {Button, Container, FormControl, Tab, Tabs} from "react-bootstrap";
import CUrl from "../../constants/CowinUrl.json";
import axios from "axios";
import config from "../../config.json";
import vaccine from "../../constants/Vaccine.json";
import moment from "moment";
import {connect} from "react-redux";
import {setToken} from "../../../../redux/user/user.actions";
import VaccineSessionTable from "./VaccineSessionTable";
import GoogleMapComponent from "../googlemap/GoogleMapComponent";
import { GetCenters } from "./CowinRequestManager";

class GetVaccineSession extends Component {
    state = {
        is_unmounted: false,
        pin_code: "",
        district_id:"1",
        is_valid_pin_code: false,
        vaccine: "",
        vaccine_date: moment().format("YYYY-MM-DD"),
        states: [],
        districts: [],
        centers: []
    }

    options = {
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': "Bearer " + this.props.user.token
        }
    }

    constructor(props) {
        super(props);
        this.GetState = this.GetState.bind(this);
        this.OnChangeState = this.OnChangeState.bind(this);
        this.OnChangeDate = this.OnChangeDate.bind(this);
        this.OnChangePinCode = this.OnChangePinCode.bind(this);
        this.OnChangeDistrictCode = this.OnChangeDistrictCode.bind(this);
        this.OnChangeVaccine = this.OnChangeVaccine.bind(this);

        this.SelectVaccinesWithDate = this.SelectVaccinesWithDate.bind(this);
        this.OnSubmitPinCode = this.OnSubmitPinCode.bind(this);
        this.OnSubmitDistrict = this.OnSubmitDistrict.bind(this);
        this.OnGetCenterCallback = this.OnGetCenterCallback.bind(this);

        this.GetVaccineSessionChild = React.createRef();
        this.googleMapRef = React.createRef();
    }

    componentDidMount() {
        // initial load
        this.GetState();
        this.OnChangeState(1)
    }

    componentWillUnmount(){
        this.setState({
            is_unmounted: true
        })
    }

    GetState(){
        axios.get(config.COWIN_BASE_URL + CUrl.GET_STATES, this.options).then( response => {
            if(response.status === 200 && response.data.states.length > 0) {
                this.setState({
                    states: response.data.states
                })
            }else{
                console.log(response);
            }
        }).catch( err => console.error(err));
    }

    OnChangeState(state){
        axios.get(config.COWIN_BASE_URL + CUrl.GET_DISTRICT.replace("<DISTRICT>", state), this.options).then( response => {
            if(response.status === 200 && response.data.districts.length > 0) {
                this.setState({
                    districts: response.data.districts
                })
            }else{
                console.error(response);
            }
        }).catch( err => console.error(err));
    }

    OnChangeDistrictCode(code){
        this.setState({
            district_id: code
        })
    }

    OnSubmitPinCode(){
        if(this.state.pin_code.length !==6) return;
        let date = moment(this.state.vaccine_date).format("DD-MM-YYYY");
        GetCenters(config.COWIN_BASE_URL + CUrl.CALENDAR_BY_PIN_CODE.replace("<PINCODE>", this.state.pin_code).replace("<DATE>", date).replace("<VACCINE>", this.state.vaccine), this.OnGetCenterCallback);
    }

    OnSubmitDistrict(){
        if(this.state.district_id.length <= 0) return;
        let date = moment(this.state.vaccine_date).format("DD-MM-YYYY");
        GetCenters(config.COWIN_BASE_URL + CUrl.CALENDAR_BY_DISTRICT.replace("<DISTRICT_ID>", this.state.district_id).replace("<DATE>", date).replace("<VACCINE>", this.state.vaccine), this.OnGetCenterCallback);
    }

    OnGetCenterCallback(centers){
        this.setState({
            centers: centers
        })
        this.GetVaccineSessionChild.current.SetCenters(this.state.centers);
        this.googleMapRef.current.UpdateMarkers(this.state.centers);
    }

    OnChangeVaccine(event){
        this.setState({
            vaccine: event.target.value
        })
    }

    OnChangeDate(event){
        this.setState({
            vaccine_date: event.target.value
        })
        this.GetVaccineSessionChild.current.OnChangeDate(event.target.value);
    }

    OnChangePinCode(event){
        this.setState({
            pin_code: event.target.value,
            is_valid_pin_code: event.target.value.length === 6
        })
    }

    SelectVaccinesWithDate() {
        return (
            <div>
                <select className={"form-control no-input-shadow mt-2"} value={this.state.vaccine} onChange={this.OnChangeVaccine}>
                    <option value="">All Vaccine</option>
                    <option value={vaccine.COVAXIN}>{vaccine.COVAXIN}</option>
                    <option value={vaccine.COVISHIELD}>{vaccine.COVISHIELD}</option>
                </select>
                <FormControl className={"no-input-shadow mt-2"} placeholder={"Enter start date"} type={"date"} value={this.state.vaccine_date} onChange={this.OnChangeDate}/>
            </div>
        );
    }

    render() {
        return (
            <Container className={"position-relative"}>
                <h5>Search Vaccine sessions with</h5>
                <Tabs defaultActiveKey="home" transition={false} id="noanim-tab-example">
                    <Tab eventKey="home" title="Pin code">
                        <div className="bg-white p-4 justify-content-center d-flex align-items-center" style={{height:300}}>
                            <div className={"col-md-6"}>
                                <FormControl className={"no-input-shadow"} placeholder={"Enter pin code"} value={this.state.pin_code} onChange={this.OnChangePinCode}/>
                                <this.SelectVaccinesWithDate />
                                <Button variant={"primary"} className={"form-control mt-4"} onClick={this.OnSubmitPinCode} disabled={!this.state.is_valid_pin_code}> Find vaccine slots</Button>
                            </div>
                        </div>
                    </Tab>
                    <Tab eventKey="profile" title="District">
                        <div className="bg-white p-4 justify-content-center d-flex align-items-center" style={{height:300}}>
                            <div className="col-md-6" >
                                <select className={"form-control no-input-shadow"} placeholder={"Select state"} onChange={e => {this.OnChangeState(e.target.value)}}>
                                    {
                                        this.state.states.map(it => <option key={it.state_id} value={it.state_id}>{it.state_name}</option>)
                                    }
                                </select>
                                <select className={"form-control no-input-shadow mt-2"} placeholder={"Select district"} value={this.state.district_id} onChange={ e => {this.OnChangeDistrictCode(e.target.value)}}>
                                    {
                                        this.state.districts.map(it => <option key={it.district_id} value={it.district_id}>{it.district_name}</option>)
                                    }
                                </select>
                                <this.SelectVaccinesWithDate />
                                <Button variant={"primary"} className={"form-control mt-4"} onClick={this.OnSubmitDistrict}> Find vaccine slots</Button>
                            </div>
                        </div>
                    </Tab>
                    {/*<Tab eventKey="near_me" title="Nearby Me">*/}
                        {/*<div className="bg-white p-4 justify-content-center d-flex align-items-center" style={{height:300}}>*/}
                        {/*    <div className="col-md-6" >*/}
                        {/*    </div>*/}
                        {/*</div>*/}
                    {/*</Tab>*/}
                </Tabs>
                {
                    // this.props.user.token? "": <Redirect to={AppUrl.REGISTER_OTP} exact={true} />
                }
                <GoogleMapComponent ref={this.googleMapRef} />
                <VaccineSessionTable ref={this.GetVaccineSessionChild} />
                <div style={{height:400}}></div>
            </Container>
        );
    }
}


const mapStateToProps = ({ user }) => (
    {
        user: user
    }
)

const mapDispatchToProps = dispatch => ({
    setToken: token => dispatch(setToken(token))
})

export default connect(mapStateToProps, mapDispatchToProps) (GetVaccineSession);
