import React, {Component} from 'react';
import {Badge, Button, OverlayTrigger, Table, Tooltip} from "react-bootstrap";
import moment from "moment";
import Constant from "../../constants/Constant.json";

class VaccineSessionTable extends Component {
    state = {
        centers: [],
        week: [0 ,1, 2, 3, 4, 5, 6].map(it=> moment().add(it, 'day').format("DD-MM-YY"))
    }

    OnChangeDate(date){
        this.setState({
            week: [0 ,1, 2, 3, 4, 5, 6].map(it=> moment(date, 'YYYY-MM-DD').add(it, 'day').format("DD-MM-YY"))
        })
    }

    SetCenters(centers){
        this.setState({
            centers: centers
        })
    }

    render() {
        return (
            <div className={"bg-light"}>
                <Table striped bordered hover>
                    <thead>
                    <tr>
                        <th>Name</th>
                        {
                            this.state.week.map(day =>
                                <th key={"th-" + day}>{day}</th>
                            )
                        }
                    </tr>
                    </thead>
                    <tbody>
                    {
                        this.state.centers.map(it =>
                            <tr key={it.center_id}>
                                <td>
                                    <div>
                                        <div className={"font-weight-bolder"}>
                                            {it.name}
                                            <sup className={"small"}>
                                                {' '}<Badge variant="secondary">{it.fee_type}</Badge>
                                            </sup>
                                        </div>
                                        <p className={"text-secondary small m-0"}>{it.address}, {it.block_name}</p>
                                        <p className={"text-secondary small m-0"}>{it.district_name}, {it.state_name}, {it.pincode}</p>
                                    </div>
                                </td>
                                {
                                    this.state.week.map(date => {
                                        let index = it.sessions.findIndex(session => session.date === moment(date, 'DD-MM-YY').format('DD-MM-YYYY'));
                                        if(index >=0){
                                            return (
                                                <td key={it.sessions[index].session_id}>
                                                    <div>
                                                        {/*{it.sessions[index].session_id}*/}
                                                        <span className={""}>{it.sessions[index].vaccine}</span>
                                                        <p className={"small m-0 text-danger"}>For {it.sessions[index].min_age_limit}+</p>
                                                        {
                                                            it.sessions[index].available_capacity ?
                                                                <OverlayTrigger
                                                                    overlay={
                                                                        <Tooltip>
                                                                            Slots: {it.sessions[index].slots.join(", ")}
                                                                        </Tooltip>
                                                                    }
                                                                >
                                                                    <a href={Constant.COWIN_REGISTRATION} target="_blank" rel="noreferrer">
                                                                        <Button variant={"success p-1"} data-toggle="tooltip" data-placement="top" title="Tooltip on top">
                                                                            Book <sup><Badge variant={"primary"}>{it.sessions[index].available_capacity}</Badge></sup>
                                                                        </Button>
                                                                    </a>
                                                                </OverlayTrigger> : <Button variant={"danger p-0"}>Booked</Button>
                                                        }
                                                    </div>
                                                </td>
                                            );
                                        }else{
                                            return (<td key={Math.random()} />);
                                        }
                                    })
                                }
                            </tr>
                        )
                    }
                    </tbody>
                </Table>
                {
                    this.state.centers.length <=0 && <div className="text-center font-weight-bold">No centers are available for now for these dates</div>
                }
            </div>
        );
    }
}

export default VaccineSessionTable;