import React, {Component} from 'react';
import GoogleMapReact from 'google-map-react';
import {Badge, Button, Col, Row} from "react-bootstrap";
import Constant from "../../constants/Constant.json";
import $ from 'jquery';
import {AddCenter, GetAllCenters, RemoveCenter} from "../vaccinesessions/IndexDbManager";
import moment from "moment";

const MyLocationComponent = () => <div>
    <div className="bg-primary" style={{height:18, width:18, borderRadius:18, border:"2px solid #fff"}}>
    </div>
</div>;

