class GoogleMapComponent extends Component {

    state = {
        my_position: {lat: 12.97, lng: 77.59},
        map_center: {lat: 12.97, lng: 77.59},
        map_zoom: 10,
        selected_center: {},
        maps_data: {},
        cw_centers: [],
        tracking_centers: [],
        show_map: false,
        markers: []
    }

    constructor(props) {
        super(props);
        this.OnMouseHoverOnHospital = this.OnMouseHoverOnHospital.bind(this);
        this.OnClickCenterBox = this.OnClickCenterBox.bind(this);
        this.LocateMyPosition = this.LocateMyPosition.bind(this);
    }

    UpdateMarkers(centers) {
        if(!this.state.show_map && centers.length > 0) {
            this.setState({
                show_map: true
            })
        }

        this.setState({
            cw_centers: centers,
        });
        this.RemoveAllMarkersFromMap();
        setTimeout(() => {this.DrawMarkersOnMap()}, 2000);
    }

    RemoveAllMarkersFromMap(){
        this.state.markers.forEach(marker => {
            marker.setMap(null);
        })
        this.setState({
            markers: []
        })
    }

    componentDidMount() {
        navigator.geolocation.getCurrentPosition((position) => {
            this.setState({
                my_position: { lat:position.coords.latitude, lng: position.coords.longitude},
                map_center: { lat:position.coords.latitude, lng: position.coords.longitude},
            })
        });

        GetAllCenters().then(item =>{
            this.setState({
                tracking_centers: item
            })
        })
    }

    LocateMyPosition(){
        this.setState({
            map_center: this.state.my_position,
            map_zoom: 11,
        })
    }

    OnMouseHoverOnHospital(hoverKey, childProps){
        if(! (childProps.center && childProps.center.name)) return;
        this.setState({selected_center: childProps.center})
    }

    HandleApiLoaded = (map, maps) => {
        this.setState({
            maps_data: {
                map: map,
                maps: maps
            }
        })
    }

    DrawMarkersOnMap(){
        if(!this.state.maps_data.map) {
            console.error("Map is not ready yet");
            return;
        }
        let unique_positions = [];
        this.state.cw_centers.forEach(it => {
            let position = { lat: it.lat, lng: it.long};
            if(unique_positions.findIndex( up => up.lat === it.lat && up.lng === it.long) >= 0) return;
            unique_positions.push(position);

            let map = this.state.maps_data.map;
            let marker = new this.state.maps_data.maps.Marker({
                position: position,
                // animation: maps.Animation.DROP,
                map,
                // icon: {
                //     url: "https://atlas-content-cdn.pixelsquid.com/stock-images/hospital-3yL2QM6-600.jpg",
                //     size: maps.Size(10, 10),
                // },
                title: it.name,
                label: {
                    text:"H",
                    color: "white",
                    fontWeight: "bold",
                }
            })

            marker.addListener("click", () => {
                marker.setAnimation(this.state.maps_data.maps.Animation.BOUNCE);
                setTimeout(()=>{marker.setAnimation(null);}, 2000);
            });

            marker.addListener("mouseover", () => {
                this.SetActiveCenterBox(it.center_id);
            })
            this.state.markers.push(marker);
        })
    }

    SetDirection(destination) {
        let directionsService = new this.state.maps_data.maps.DirectionsService();
        let directionsRenderer = new this.state.maps_data.maps.DirectionsRenderer();
        directionsRenderer.setMap(this.state.maps_data.map);

        let request = {
            origin: this.state.my_position,
            destination: destination,
            travelMode: this.state.maps_data.maps.TravelMode.DRIVING,
        };

        directionsService.route(request, function(response, status) {
            if (status === 'OK') {
                directionsRenderer.setDirections(response);
            }
        });
    }

    SetActiveCenterBox(center_id) {
        $('.center-box').removeClass('border-primary');
        $('#center-'+center_id).addClass('border-primary');
    }

    OnClickCenterBox(center){
        this.SetActiveCenterBox(center.center_id);
        this.setState({
            map_center: {lat: center.lat, lng: center.long},
            map_zoom: 9,
        })
    }

    RemoveFromTracking(pin_code) {
        if(!this.IsTrackingContains(pin_code)) return;
        RemoveCenter(pin_code);

        let tracking_center = this.state.tracking_centers;
        tracking_center[tracking_center.findIndex(it => it.pin_code = pin_code)] = tracking_center[tracking_center.length -1];
        tracking_center.pop();

        this.setState({
            tracking_center: tracking_center
        })
    }

    AddToTracking(pin_code) {
        if(this.IsTrackingContains(pin_code)) return;
        AddCenter(pin_code);
        let tracking_center = this.state.tracking_centers;
        tracking_center.push({pin_code: pin_code});
        this.setState({
            tracking_center: tracking_center
        })
    }

    IsTrackingContains(pin_code){
        return (this.state.tracking_centers.findIndex(center => center.pin_code === pin_code) >=0);
    }

    GetFormattedDate(date){
        return moment(date, "DD-MM-YYYY").format("DD MMM");
    }
    render() {
        return (
            <section>
                <Row>
                    <Col md={9} className={""}>
                        {
                            (this.state.show_map > 0) &&
                            <div style={{height: 600, width: "100%"}} className={"shadow-sm position-relative"}>
                                <GoogleMapReact
                                    bootstrapURLKeys={{key: Constant.GOOGLE_MAP_API, language: "en"}}
                                    defaultCenter={{lat: 12.97, lng: 77.59}}
                                    center={this.state.map_center}
                                    defaultZoom={10}
                                    zoom={this.state.map_zoom}
                                    yesIWantToUseGoogleMapApiInternals={true}
                                    onGoogleApiLoaded={({map, maps}) => this.HandleApiLoaded(map, maps)}
                                    onChildMouseEnter={this.OnMouseHoverOnHospital}
                                >
                                    <MyLocationComponent
                                        lat={this.state.my_position.lat}
                                        lng={this.state.my_position.lng}
                                    />
                                </GoogleMapReact>
                                <Button variant={"light"} className="my-location position-absolute shadow" style={{right: 10, bottom:150, height: 38, width:38, borderRadius: 38}} onClick={this.LocateMyPosition}>
                                    +
                                </Button>
                            </div>
                        }
                    </Col>
                    <Col md={3} style={{maxHeight: 600, overflowY:"auto"}}>
                        <Col className={"p-0 py-1"} >
                            <div className={"shadow p-1 rounded"}>
                                {
                                    this.state.tracking_centers.length > 0?
                                        <h6 className={"font-weight-bold"}> Tracking pin codes for age &lt; 45</h6>:
                                        <h6 className={"font-weight-bold"}> No Trackers</h6>
                                }
                                <hr />
                                {
                                    this.state.tracking_centers.map(t_center =>{
                                        return (
                                            <div className={"rounded p-2 m-1 font-weight-bold bg-white position-relative overflow-hidden"} key={"center-tr-" + t_center.pin_code}>
                                                {t_center.pin_code}
                                                <Button variant={"danger"} onClick={() => {
                                                    this.RemoveFromTracking(t_center.pin_code)
                                                }} className={"float-right  position-absolute rounded-0"} style={{top:0, right:0, height:"100%"}}> X </Button>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </Col>
                        {
                            (this.state.cw_centers.length === 0) && <Col className={"p-0 py-1 font-weight-bold"}>
                                <div className={"p-1 shadow rounded border center-box"}>
                                    No centers are available now
                                </div>
                            </Col>
                        }
                        {
                            this.state.cw_centers.map(center =>
                                <Col className={"p-0 py-1"} key={"center-" + center.center_id}>
                                    <div className={"p-1 shadow rounded border center-box"}
                                         id={"center-" + center.center_id} onClick={() => {
                                        this.OnClickCenterBox(center)
                                    }}>
                                        <b>{center.name}{' '}
                                            <sup>
                                                {
                                                    center.sessions[0].available_capacity ? <Badge
                                                            variant={"primary"}>{center.sessions[0].available_capacity}</Badge> :
                                                        <Badge variant={"danger"}>Booked</Badge>
                                                }
                                            </sup>
                                        </b>
                                        <p className={"text-secondary small m-0"}>{center.address}, {center.block_name}</p>
                                        <p className={"text-secondary small m-0"}>{center.district_name}, {center.state_name}, {center.pincode}</p>
                                        {
                                            center.sessions.map(session =>
                                                <div className={"font-weight-bolder"} key={session.session_id}>
                                                    <span
                                                        className={"small font-weight-bold"}>{this.GetFormattedDate(session.date)} [{session.vaccine}, {session.min_age_limit}+] {' '} </span>
                                                    {
                                                        session.available_capacity ?
                                                            <a href={Constant.COWIN_REGISTRATION} target="_blank"
                                                               rel="noreferrer">
                                                                <Badge variant={"primary"}
                                                                       className={"btn"}>Book</Badge>
                                                            </a> : <Badge variant={"danger"}>Booked</Badge>
                                                    }
                                                </div>
                                            )
                                        }
                                        <div className={"text-primary btn px-0 small"} onClick={() => {this.SetDirection({lat: center.lat, lng: center.long})}}
                                             style={{fontSize:11}}>
                                            Show direction on map
                                        </div>
                                        <div>
                                            {
                                                this.IsTrackingContains(center.pincode) ?
                                                    "" :
                                                    <Badge variant={"primary"} className={"btn p-1 m-1"} onClick={() => {
                                                        this.AddToTracking(center.pincode)
                                                    }}> Set Tracking</Badge>
                                            }
                                        </div>
                                    </div>
                                </Col>
                            )
                        }
                    </Col>
                </Row>

                {/*<TrackModal*/}
                {/*    show={this.state.modalShow}*/}
                {/*    onHide={() => this.SetModalShow(false)}*/}
                {/*/>*/}
            </section>
        );
    }
}

export default GoogleMapComponent;