import { useIndexedDB } from 'react-indexed-db';

const DBConfig = {
    name: 'cowin',
    version: 1,
    objectStoresMeta: [
        {
            store: 'centers',
            storeConfig: { keyPath: 'id', autoIncrement: true },
            storeSchema: [
                { name: 'pin_code', keypath: 'pin_code', options: { unique: false } },
            ]
        }
    ]
}

function GetAllCenters() {
    const { getAll } = useIndexedDB('centers');
    return getAll();
}

function AddCenter(pinCode) {
    const { add } = useIndexedDB('centers');
    return add({
                pin_code: pinCode
            });
}

function RemoveCenter(pinCode) {
    const { deleteRecord  } = useIndexedDB('centers');
    GetAllCenters().then(item=>{
        item.forEach(it=> {
                if (it.pin_code === pinCode) {
                    return deleteRecord(it.id);
                }
            }
        )
    })
}

export {AddCenter, GetAllCenters, RemoveCenter, DBConfig};